.steppersContent{ /* Must have */
    display: block;
    margin-bottom: 0;
    width: 80%;
    height: 1500px; /* Need to change according to different site*/
}

@media only screen and (max-width: 2240px) {
    .steppersContent{
        height: 1600px;
    }
}

@media only screen and (min-width: 2060px) { /* Must Have cannot change */
    .steppersContent{
        width: 85%;
    }
}

@media only screen and (max-width: 1480px) {
    .steppersContent{
        height: 1700px;
    }
}

@media only screen and (max-width: 1250px) {
    .steppersContent{
        width: 100%;
    }
}

