.image600{
    width: 600px;
}

.image400{
    width: 400px;
}

@media only screen and (max-width: 1440px) {
    .image600{
        width: 400px;
    }

    .image400{
        width: 250px;
    }
}

@media only screen and (max-width: 420px) {
    .image600{
        width: 300px;
    }
}

@media only screen and (max-width: 340px) {
    .image600{
        width: 250px;
    }
}